.buttonContainer {
  position: relative;
}

.buttonForm {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 10px;
  margin-top: 20px;
  background-color: #fff;
  color: black;
  width: 100%;
  max-width: 400px;
  border-radius: 12px;
  padding: 20px;
  cursor: pointer;
  border: 1px solid #9e9e9e1f;
}

.separatorLine {
  position: absolute;
  top: -20px;
  left: 0;
  right: 0;
  height: 1px;
  background-color: #9e9e9e;
}

.orText {
  position: absolute;
  top: -20px;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  padding: 0 10px;
  color: #9e9e9e;
}
.icon {
  height: 16px;
  width: 16px;
  margin-right: 10px;
}
